import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IDeposito, Response } from '../models/deposito';
import { AuthService } from '@core/auth/auth.service';

@Injectable()
export class DepositosService {
    constructor(private http: HttpClient, private _authService:AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Depositos.
     * @returns Depositos
     */
    getDepositos(sort?:any): Observable<Response[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
        .subscribe((resp:any) => {
            this._authService.accessToken = resp.token,
            subject.next({
                'list':resp.result,
                // 'totalPages': resp.headers.get('x-pagination-page-count'),
                // 'totalCount': resp.headers.get('x-pagination-total-count')
            })
        });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param deposito 
     * @returns Un deposito nuevo
     */
    saveDeposito(deposito: IDeposito): Observable<IDeposito> {
        return this.http.post<IDeposito>(`${environment.stock.depositos}`, deposito);
    }

    /**
     * Funcion en el servicio
     * @param deposito
     * @returns Deposito actualizado
     */
    updateDeposito(deposito: IDeposito): Observable<IDeposito> {
        return this.http.put<IDeposito>(`${environment.stock.depositos}` + '/' + deposito.iddeposito, deposito);
    }

    /**
     * Funcion en el servicio
     * @param iddeposito 
     * @returns Elimina el deposito
     */
    deleteDeposito(iddeposito: number): Observable<IDeposito> {
        return this.http.delete<IDeposito>(environment.stock.depositos + '/' + iddeposito);
    }
}